.ct-chart{}
.ct-chart .ct-label,
.ct-chart .ct-label.ct-horizontal {
    display: block;
    width: 100%;
    height: 100%;
    fill: #333;
    color: rgba(0,0,0,.4);
    font-size: 12px;
    text-align: left;
}
.ct-chart .ct-label.ct-vertical {
    display: block;
    width: 100%;
    height: 100%;
    fill: rgba(0,0,0,.4);
    color: rgba(0,0,0,.4);
    font-size: 12px;
    text-align: right;
}
.ct-chart .ct-grid {
    stroke: rgba(0,0,0,.2);;
    stroke-width: 1px;
    stroke-dasharray: 2px;
}
.ct-chart .ct-point {
    stroke-width: 10px;
    stroke-linecap: round;
}
.ct-chart .ct-line {
    fill: none;
    stroke-width: 4px;
}
.ct-chart .ct-area {
    stroke: none;
    fill-opacity: .1;
}
.ct-chart .ct-bar {
    fill: none;
    stroke-width: 10px;
}
.ct-chart .ct-slice.ct-donut {
    fill: none;
    stroke-width: 60px;
}
