.ct-chart.ct-square {
    display: block;
    position: relative;
    width: 100%;
}
.ct-chart.ct-square:before {
    display: block;
    float: left;
    content: "";
    width: 0;
    height: 0;
    padding-bottom: 100%;
}
.ct-chart.ct-square:after {
    content: "";
    display: table;
    clear: both;
}
.ct-chart.ct-square>svg {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
}
